import React from 'react';
import { Element } from 'react-scroll';
import GitHubCalendar from 'react-github-calendar';
import { Typewriter } from 'react-simple-typewriter';
import './Section.css';

const Home = () => {
    return (
        <Element name="home" className="section">
            <h1> ,  </h1>
            <div className="content-container">
                <p>
                    <Typewriter
                        words={[' , API,   BE ,        .']}
                        loop={1}
                        cursor
                        cursorStyle="_"
                        typeSpeed={50}
                        deleteSpeed={50}
                        delaySpeed={1000}
                    />
                </p>
                <p>
                    <Typewriter
                        words={['    ,       .']}
                        loop={1}
                        cursor
                        cursorStyle="_"
                        typeSpeed={50}
                        deleteSpeed={50}
                        delaySpeed={1000}
                    />
                </p>
                <p>
                    <Typewriter
                        words={['    ,            .']}
                        loop={1}
                        cursor
                        cursorStyle="_"
                        typeSpeed={50}
                        deleteSpeed={50}
                        delaySpeed={1000}
                    />
                </p>
                <p>
                    <Typewriter
                        words={['     ,        .']}
                        loop={1}
                        cursor
                        cursorStyle="_"
                        typeSpeed={50}
                        deleteSpeed={50}
                        delaySpeed={1000}
                    />
                </p>
            </div>
            <div className="github-graph">
                <GitHubCalendar
                    username="mine702"
                    color="#0969da" //     
                    style={{
                        margin: 20,
                        display: 'block',
                        maxWidth: '100%',
                        height: 'auto',
                        overflow: 'visible',
                    }} />
            </div>
        </Element>
    );
};

export default Home;
